import * as React from 'react';
import { useState, useEffect } from 'react';
import {useParams} from "react-router-dom"
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Container,Typography,
  TextField,
  Avatar,
  CardActions,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import axios from 'axios';
import { variables } from "../../variables";




const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));



const states = [
  {
    value: 'alabama',
    label: 'Alabama'
  },
  {
    value: 'new-york',
    label: 'New York'
  },
  {
    value: 'san-francisco',
    label: 'San Francisco'
  }
];

const user = {
  avatar: '/static/images/avatars/avatar_6.png',
  city: 'Los Angeles',
  country: 'USA',
  jobTitle: 'Senior Developer',
  name: 'Katarina Smith',
  timezone: 'GTM-7'
};
export default function Profile(props) {
  const params = useParams()
  const userId = params.userId

  // const [values, setValues] = useState({
  //   user: userId,
  //   firstName: 'Katarina',
  //   lastName: 'Smith',
  //   email: 'demo@devias.io',
  //   phone: '',
  //   state: 'Alabama',
  //   country: 'USA'
  // });

  const [values, setValues] = useState({});

  // const [Titles, setTitles] = useState([]);
    const getTitles = async () => {
        try {
            const Endpoint = `FullProfile/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setValues(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        getTitles();
    }, []);



  const handleChange = (event) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };
  return (
    <>
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        py: 8
      }}
    >
      <Container maxWidth="lg">
        <Typography
          sx={{ mb: 3 }}
          variant="h4"
        >
          Employee Profile {values.user}
        </Typography>
        <form
      autoComplete="off"
      noValidate
      {...props}
    >
        <Grid
          container
          spacing={3}
        >
          <Grid
            item
            lg={4}
            md={6}
            xs={12}
          >
       
       <Card {...props}>
    <CardContent>
      <Box
        sx={{
          alignItems: 'center',
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        <Avatar
          src={user.avatar}
          sx={{
            height: 64,
            mb: 2,
            width: 64
          }}
        />
        <Typography
          color="textPrimary"
          gutterBottom
          variant="h5"
        >
          {values.fullname}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {`${values.email}`}
          
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {values.cellphone}, {values.nationality}
        </Typography>
      </Box>
    </CardContent>
    <Divider />
    <CardActions>
      <Button
        color="primary"
        fullWidth
        variant="text"
      >
        {/* Upload picture */}
      </Button>
    </CardActions>
  </Card>
          </Grid>
          <Grid
            item
            lg={8}
            md={6}
            xs={12}
          >
      
      <Card>
        <CardHeader
          // subheader="The information can be edited"
          title={`Profile ${userId}`}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >Hire date
              <TextField
                fullWidth
                name="hireDate"
                value={values.hireDate}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >Calendar
              <TextField
                fullWidth
                name="calendar"
                required
                value={values.calendar}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >Marital Status
              <TextField
                fullWidth
                name="marialStatus"
                value={values.marialStatus}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >Gender
              <TextField
                fullWidth
                name="gender"
                value={values.gender}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >Payrol Currency
              <TextField
                fullWidth
                name="payrollCurrenty"
                required
                value={values.payrollCurrenty}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >Salary
              <TextField
                fullWidth
                name="salary"
                required
                value={values.salary}
                variant="outlined"
              >
                {states.map((option) => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
            p: 2
          }}
        >
          {/* <Button
            color="primary"
            variant="contained"
          >
            Save details
          </Button> */}
        </Box>
      </Card>
 
  
          </Grid>
        </Grid>
        </form>
      </Container>
    </Box>
  </>
  );
}